import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home/home'
import IntelligentCarWashing from '../views/Productintroduction/Productintroduction'
import about from '../views/about/about'
import joinme from '../views/joinme/joinme'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: "浙江俊驰智能科技有限公司-首页"
    }
  },
  {
    path: '/Productintroduction',
    name: 'Productintroduction',
    component: IntelligentCarWashing,
    meta: {
      title: "浙江俊驰智能科技有限公司-产品介绍"
    }
  },
  {
    path: '/about',
    name: 'about',
    component: about,
    meta:{
      title:"浙江俊驰智能科技有限公司-关于我门"
    }
  },
  {
    path: '/joinme',
    name: 'joinme',
    component: joinme,
    meta:{
      title:"浙江俊驰智能科技有限公司-加入我们"
    }
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
