<template>
  <div class="big-box">
    <div class="header-logo" v-show="nav">
      <!-- logo -->
      <div class="margin-left">
        <h2>六合骏驰</h2>
        <span>Liuhe junchi</span>
      </div>
      <!-- 導航盒子 -->
      <div class="header-nav">
        <el-menu
          :default-active="$route.path"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          text-color="#143379"
          active-text-color="#F07706"
          router
        >
          <el-menu-item index="/">
            <i class="iconfont icon-index"></i>
            首页
          </el-menu-item>
          <el-menu-item index="/Productintroduction">
            <i class="iconfont icon-chanpinjieshao"></i>
            产品简介
          </el-menu-item>
          <el-menu-item index="/about">
            <i class="iconfont icon-guanyuwomen"></i>
            关于我们
          </el-menu-item>
          <el-menu-item index="/joinme">
            <i class="iconfont icon-jiaruwomen"></i>
            加入我们
          </el-menu-item>
        </el-menu>
      </div>
    </div>
    <!-- 導航2 -->
    <div class="header-nav2" v-show="nav2">
      <el-menu
        :default-active="$route.path"
        class="el-menu-demo2"
        mode="horizontal"
        @select="handleSelect"
        text-color="#143379"
        active-text-color="#F07706"
        router
      >
        <el-menu-item index="/">
          <i class="iconfont icon-index"></i>
          首页
        </el-menu-item>
        <el-menu-item index="/Productintroduction">
          <i class="iconfont icon-chanpinjieshao"></i>
          产品简介
        </el-menu-item>
        <el-menu-item index="/about">
          <i class="iconfont icon-guanyuwomen"></i>
          关于我们
        </el-menu-item>
        <el-menu-item index="/joinme">
          <i class="iconfont icon-jiaruwomen"></i>
          加入我们
        </el-menu-item>
      </el-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      activeIndex: "",
      nav: true,
      nav2: false,
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    window.addEventListener("scroll", this.scrollToTop, true);
  },
  methods: {
    handleSelect(index) {
      this.activeIndex = index;
    },
    // 显示按钮的显示与隐藏
    scrollToTop() {
      const that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
        if (that.scrollTop > 60) {
          that.nav2 = true;
          that.nav = false;
        } else {
          that.nav2 = false;
          that.nav = true;
        }
    },
  },
  watch: {
    $route() {
      this.handleSelect(this.activeIndex);
    },
  },
};
</script>

<style lang='less' scoped>
