    .el-submenu__title:hover {
      background-color: rgb(208, 103, 59) !important;
      color: white;
    }
    .el-menu-item:hover {
      background-color: rgb(208, 103, 59) !important;
      color: white;
    }
    .el-menu--horizontal > .el-menu-item {
      height: 80% !important;
    }
    i {
      color: rgba(7, 86, 166);
      font-size: 30px;
    }
  }
}
// 道航2
.header-nav2 {
  display: flex;
  justify-content: center;
